<template>
  <div>
    <headerBar :header-data="headerData" />
    <div class="container">
      <div v-for="(items,indexs) of allList" :key="indexs">
        <div class="content">
          <div class="work-rizhi-rymc">
            <i class="iconfont iconwode-select" />
            {{ items.rymc }}
          </div>
        </div>
        <van-collapse v-model="activeNames">
          <div v-if="items.rzList && items.rzList.length>0">
            <!-- 循环列表 begin -->
            <div v-for="(item,index) of items.rzList" :key="index" class="content">
              <div class="common-main box">
                <van-collapse-item :name="item.id">
                  <template #title>
                    <div class="work-rizhi-date">
                      <van-checkbox v-model="item.checked" :disabled="item.spzt===3" @click.stop.native="onChange(item)" />
                      <h4>{{ formatDate12(item.rzsj) }} {{ getWeek(item.rzsj) }}</h4>
                    </div>
                  </template>
                  <div class="rzdetail-content">
                    <div v-for="(item1,index1) of item.rzmxArr" :key="index1" class="rzdetail-list">
                      <van-cell title="分类" :value="item1.gzxzmc" />
                      <van-cell v-if="item1.xmmc && item1.xmmc!=='请选择项目'" title="项目" :value="item1.xmmc" />
                      <div class="page-list block">
                        <div class="pagelist-left">内容</div>
                        <div class="pagelist-right">
                          <p>{{ item1.gznr }}</p>
                        </div>
                      </div>
                      <van-cell title="工作量(小时)" :value="item1.gzl" />
                      <van-field
                        v-model="item1.sdgzl"
                        input-align="right"
                        label="审定工作量(小时)"
                        placeholder="请输入工作量"
                        :readonly="item1.zt===1"
                      />
                    </div>
                  </div>
                  <div class="txyj audit-opinion">
                    <div class="txyj-tit">
                      审批意见
                      <span class="more" @click="goXzrzCyy(index,indexs,item)">选择常用语
                        <i class="iconfont iconarrowright" />
                      </span>
                    </div>
                    <div class="txyj-content">
                      <textarea v-model="item.spyj" rows="2" placeholder="请输入审核意见" />
                    </div>
                  </div>
                </van-collapse-item>
              </div>
            </div>
            <!-- 循环列表 end -->
          </div>
        </van-collapse>

        <!-- 选择部门-人员弹窗 begin -->
        <van-popup v-model="RenYuanshow" round position="bottom">
          <van-cascader
            v-model="cascaderValue"
            class="cascader-flex"
            title="选择人员"
            :options="options"
            @close="RenYuanshow = false"
            @finish="onFinish"
          />
        </van-popup>
        <!-- 选择部门-人员弹窗 end -->

        <!-- 按钮 begin -->
        <!-- <div class="foot-btn-white">
        <van-button type="info" block plain size="large"> 近期日志 <i class="iconfont iconarrowright" /></van-button>
      </div> -->
        <!-- 按钮 end -->
      </div>
      <div class="common-space" />
      <div v-if="tySpyj" class="foot-btn-tips fixed">
        <van-notice-bar
          color="#1890ff"
          background="#ecf9ff"
          left-icon="comment-o"
          :text="tySpyj"
        />
      </div>
      <!-- 底部按钮 begin -->
      <div class="foot-btn full fixed noshadow">
        <ul>
          <li>
            <div class="foot-option">
              <div class="foot-item" @click="showcgfsPopup()">
                <i class="iconfont iconbiaoge-tijiao" />
                选择常用语
              </div>
              <div class="foot-item" @click="goShGzrzJL()">
                <i class="iconfont iconjilu" />
                审核记录
              </div>
            </div>
          </li>
          <!-- <li class="btn-spe1"><van-button type="info" round size="large" @click="getRzList()">搜索</van-button></li> -->
          <!-- <li class="btn-spe1"><van-button type="info" round size="large" @click="thGzrz">退回</van-button></li> -->
          <li><van-button type="info" round size="large" @click="tjGzrz">提交</van-button></li>
        </ul>
      </div>
      <!-- 底部按钮 end -->

    </div>

    <van-dialog
      v-model="show3"
      title="审批成功！"
      :show-cancel-button="true"
      confirm-button-text="返回首页"
      cancel-button-text="继续审批"
      @confirm="spwc()"
      @cancel="jxsp()"
    />
    <searchPick
      :pick-title="searchPick.PickTitle"
      :pick-columns="searchPick.PickColumns"
      :show-button="showButton"
      @confirmClick="confirmPick($event)"
    />
  </div>
</template>

<script land="script" scoped>
import headerBar from '@/components/header/headerBar.vue'
import { getUserName } from '@/common/js/catch'
import searchPick from '@/components/searchComponents/searchPick.vue'
import { pageRouter } from '@/common/js/mixin.js'

export default {
  name: 'ShGzrz',
  components: {
    headerBar,
    searchPick
  },
  mixins: [pageRouter],
  data() {
    return {
      tySpyj: '',
      showButton: false,
      rzList: [],
      date1: '请选择日期',
      show1: false,
      checked: false,
      cascaderValue: '',
      headerData: {
        title: '审核工作日志',
        leftUrl: 'ShGzrzYl',
        leftText: ' '
      },
      show2: false,
      maxDate: new Date(2100, 0, 1),
      minDate: new Date(1999, 0, 1),
      minDate1: new Date(1999, 0, 1),
      date: '请选择日期',
      renyuan: '请选择人员',
      show: false,
      RenYuanshow: false,
      activeNames: ['1'],
      options: [],
      ksrq: '',
      jsrq: '',
      rymc: '',
      checkRzList: [],
      show3: false,
      allList: [],
      searchPick: {
        PickTitle: '',
        PickColumns: []
      },
      ryxx: {},
      buttonState: true
    }
  },
  created() {
    if (window.localStorage.getItem('cyyList')) {
      const cyyList = JSON.parse(window.localStorage.getItem('cyyList'))
      const maps = []
      for (let i = 0; i < cyyList.length; i++) {
        maps.push({ 'value': cyyList[i], 'label': cyyList[i] })
      }
      this.searchPick.PickColumns = maps
    }
    if (this.searchPick.PickColumns.length === 0) {
      this.showButton = true
    } else {
      this.showButton = false
    }

    if (this.$store.state.erp.list) {
      this.allList = this.$store.state.erp.list
      if (this.$route.params.cyy) {
        this.allList[this.$store.state.erp.checkIndex1].rzList[this.$store.state.erp.checkIndex].spyj = this.$route.params.cyy
        this.allList.splice(0, 1, this.allList[0])
      }
    } else {
      if (this.$store.state.erp.ry) {
        this.renyuan = this.$store.state.erp.ry.rymc
        this.rymc = this.$store.state.erp.ry.ryid
        this.ryxx = this.$store.state.erp.ry
        this.getShryList()
      } else {
        this.getShryList()
      }
    }
  },
  activated() {
    if (window.localStorage.getItem('cyyList')) {
      const cyyList = JSON.parse(window.localStorage.getItem('cyyList'))
      const maps = []
      for (let i = 0; i < cyyList.length; i++) {
        maps.push({ 'value': cyyList[i], 'label': cyyList[i] })
      }
      this.searchPick.PickColumns = maps
    }
    if (this.searchPick.PickColumns.length === 0) {
      this.showButton = true
    } else {
      this.showButton = false
    }

    if (this.$store.state.erp.list) {
      this.allList = this.$store.state.erp.list
      if (this.$route.params.cyy) {
        this.allList[this.$store.state.erp.checkIndex1].rzList[this.$store.state.erp.checkIndex].spyj = this.$route.params.cyy
        this.allList.splice(0, 1, this.allList[0])
      }
    } else {
      if (this.$store.state.erp.ry) {
        this.renyuan = this.$store.state.erp.ry.rymc
        this.rymc = this.$store.state.erp.ry.ryid
        this.ryxx = this.$store.state.erp.ry
        this.getShryList()
      } else {
        this.getShryList()
      }
    }
  },
  methods: {
    jxsp() {
      this.$router.push('ShGzrzYl')
    },
    onChange(item) {
      if (item.spzt === 3) {
        this.$toast('当前日志已归档，不可操作！')
        return
      }
      this.allList.splice(0, 1, this.allList[0])
    },
    confirmPick(pick) {
      const cyy = pick.value
      for (let i = 0; i < this.allList.length; i++) {
        for (let j = 0; j < this.allList[i].rzList.length; j++) {
          this.allList[i].rzList[j].spyj = cyy
        }
      }
      this.tySpyj = cyy
      this.allList.splice(0, 1, this.allList[0])
    },
    showcgfsPopup() {
      this.$bus.$emit('showPick', true)
    },
    spwc() {
      this.show3 = false
      this.$router.push('RzIndex')
    },
    getShryList1() {
      this.allList = []
      this.$store.dispatch('erp/getShryAction', {
        ryid: getUserName()
      }).then(res => {
        for (let i = 0; i < res.length; i++) {
          this.getGzrzList(res[i].ryid, res[i])
        }
      }).catch(error => {
        this.$toast(error)
      })
    },
    getShryList() {
      this.allList = []
      this.$store.dispatch('erp/getShryAction', {
        ryid: getUserName()
      }).then(res => {
        if (this.$store.state.erp.ry && this.$store.state.erp.ry.ryid) {
          for (let i = 0; i < res.length; i++) {
            if (res[i].ryid === this.$store.state.erp.ry.ryid) {
              this.getGzrzList(res[i].ryid, res[i])
            }
          }
        } else {
          for (let i = 0; i < res.length; i++) {
            this.getGzrzList(res[i].ryid, res[i])
          }
        }
      }).catch(error => {
        this.$toast(error)
      })
    },
    getGzrzList(rymc, temp) {
      this.$store.dispatch('erp/getGzrzAction', {
        ksrq: this.ksrq,
        jzrq: this.jsrq,
        ryid: rymc,
        shbz: [1, 3]
      }).then(res => {
        for (let i = 0; i < res.gzrzArr.length; i++) {
          for (let j = 0; j < res.gzrzArr[i].rzmxArr.length; j++) {
            if (!res.gzrzArr[i].rzmxArr[j].sdgzl) {
              res.gzrzArr[i].rzmxArr[j].sdgzl = res.gzrzArr[i].rzmxArr[j].gzl
            }
          }
        }
        if (res.gzrzArr && res.gzrzArr.length > 0) {
          for (let i = 0; i < res.gzrzArr.length; i++) {
            if (res.gzrzArr[i].spzt !== 3) {
              res.gzrzArr[i].checked = true
            }
          }
          temp.rzList = res.gzrzArr
          this.allList.push(temp)
          this.checkRzList = this.allList
        }
      }).catch(error => {
        console.log('error==============================', error)
        this.$toast(error)
      })
    },
    thGzrz() {
      if (!this.checkRzList || this.checkRzList.length === 0) {
        this.$toast('请先选择日志！')
        return
      }
      const rqarr = []
      for (let i = 0; i < this.checkRzList.length; i++) {
        rqarr.push(this.checkRzList[i].rzsj)
      }
      this.$store.dispatch('erp/backShrzAction', {
        ryid: this.rymc,
        rqArr: rqarr
      }).then(res => {
        this.$toast('退回日志成功！')
      }).catch(error => {
        this.$toast(error)
      })
    },
    tjGzrz() {
      if (!this.buttonState) {
        return
      }
      this.buttonState = false
      this.checkRzList = []
      for (let m = 0; m < this.allList.length; m++) {
        for (let i = 0; i < this.allList[m].rzList.length; i++) {
          if (this.allList[m].rzList[i].checked) {
            this.checkRzList.push(this.allList[m].rzList[i])
          }
        }
      }
      if (!this.checkRzList || this.checkRzList.length === 0) {
        this.buttonState = true
        this.$toast('请先选择日志！')
        return
      }
      for (let i = 0; i < this.checkRzList.length; i++) {
        if (this.checkRzList[i].spyj) {
          this.checkRzList[i].spyj = this.checkRzList[i].spyj.trim()
        }
        if (!this.checkRzList[i].spyj) {
          this.buttonState = true
          this.$toast('请输入审批意见！')
          return
        } else {
          this.checkRzList[i].shjy = this.checkRzList[i].spyj
        }
        for (let j = 0; j < this.checkRzList[i].rzmxArr.length; j++) {
          if (!this.checkRzList[i].rzmxArr[j].sdgzl || this.checkRzList[i].rzmxArr[j].sdgzl <= 0) {
            this.$toast('请输入正确工作量！')
            this.buttonState = true
            return
          }
        }
      }
      this.$store.dispatch('erp/saveShrzAction', {
        shryid: getUserName(),
        rzArr: this.checkRzList
      }).then(res => {
        this.$toast('审核日志成功！')
        this.show3 = true
        this.getShryList()
        this.buttonState = true
      }).catch(error => {
        this.buttonState = true
        this.$toast(error)
      })
    },
    getWeek(date) {
      date = date + ''
      const str = date.slice(0, 4) + '-' + date.slice(4, 6) + '-' + date.slice(6, 8)
      const temp = new Date(str)
      if (temp.getDay() === 1) {
        return '周一'
      } else if (temp.getDay() === 2) {
        return '周二'
      } else if (temp.getDay() === 3) {
        return '周三'
      } else if (temp.getDay() === 4) {
        return '周四'
      } else if (temp.getDay() === 5) {
        return '周五'
      } else if (temp.getDay() === 6) {
        return '周六'
      } else if (temp.getDay() === 0) {
        return '周日'
      }
    },
    formatDate(date) {
      return `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()}`
    },
    formatDate1(date) {
      const year = date.getFullYear()
      let month = ''
      let day = ''
      if ((date.getMonth() + 1) < 10) {
        month = '0' + (date.getMonth() + 1)
      } else {
        month = date.getMonth() + 1
      }
      if (date.getDate() < 10) {
        day = '0' + (date.getDate())
      } else {
        day = date.getDate()
      }
      return year + '' + month + day
    },
    formatDate12(date) {
      date = date + ''
      return date.slice(0, 4) + '-' + date.slice(4, 6) + '-' + date.slice(6, 8)
    },
    onConfirm(date) {
      this.ksrq = this.formatDate1(date)
      this.date = this.formatDate(date)
      this.minDate1 = date
      this.show = false
    },
    onConfirm1(date) {
      this.jsrq = this.formatDate1(date)
      this.date1 = this.formatDate(date)
      this.maxDate = date
      this.show1 = false
    },
    // 全部选项选择完毕后，会触发 finish 事件
    onFinish({ selectedOptions }) {
      this.rymc = selectedOptions[0].value
      this.RenYuanshow = false
      this.renyuan = selectedOptions.map((option) => option.text).join('/')
    },
    goXzrzCyy(index, index1, item) {
      if (item.spzt === 3) {
        this.$toast('当前日志已归档，不可操作！')
        return
      }
      this.$store.commit('erp/SET_LIST', this.allList)
      this.$store.commit('erp/SET_CHECKINDEX', index)
      this.$store.commit('erp/SET_CHECKINDEX1', index1)
      this.$router.push({
        name: 'XzrzCyy'
      })
    },
    goShGzrzJL() {
      this.$store.commit('erp/SET_LIST', this.allList)
      this.$router.push({
        name: 'ShGzrzJL'
      })
    }
  }
}
</script>

<style scoped>
.common-main{padding: 5px 10px;}
.van-collapse-item--border::after{display: none;}
.van-cell::after{display: none;}
/deep/.van-field__label{width: auto;}
.txyj{margin: 0 5px;}
</style>
